.subscription-form-modal {
    margin: 0 auto;
    width: 50vw;
    height: 40vh;
    text-align: center;
    form {
        label {
            display: block;
            font-size: 1.5rem;
            margin: 0.5rem auto 0.25rem;
        }
        .subscription-select {
            color: #000;
            background-color: #00073d;
            background-color: #c3c3c3;
            padding: 0.45rem 0.75rem;
            width: 160px;
            font-size: 1.175rem;
            border-radius: 4px;
            outline: none;
            border: none;
            option {
                background-color: #e2e2e2;
                color: #000;
            }
        }
        .subsType {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            p {
                font-size: 1.15rem;
                margin: 0.3rem 0.35rem 0.75rem;
                padding: 0.35rem 0.65rem;
                border-radius: 4px;
                background-color: rgb(195, 195, 195);
                cursor: pointer;
                transition: .225s ease-in-out;
                &:hover {
                    background-color: #000;
                    color: #fff;
                }
                &.active {
                    background-color: rgb(0, 20, 80);
                    color: #fff;
                }
            }
        }
    }
    button {
        width: 150px;
        border: 1px solid #000;
        padding: 0.8rem 1.25rem;
        background-color: transparent;
        margin: 0.25rem 0.5rem 0.75rem;
        text-transform: uppercase;
        font-weight: 700;
        cursor: pointer;
        transition: .2s ease-in-out;
        &:hover {
            background-color: #000;
            color: #fff;
        }
    }
}

.input-container {
    position: relative;
    margin: 1.5rem auto;
    width: 200px;
}

.input-container input[type="number"] {
    font-size: 20px;
    width: 100%;
    border: none;
    border-bottom: 2px solid #ccc;
    padding: 5px 0;
    background-color: transparent;
    outline: none;
}

.input-container .label {
    font-size: 1.15rem;
    position: absolute;
    top: 0;
    left: 0;
    color: #ccc;
    transition: all 0.3s ease;
    pointer-events: none;
}

.input-container input[type="number"]:focus~.label,
.input-container input[type="number"]:valid~.label {
    top: -25px;
    font-size: 16px;
    color: #333;
}

.input-container .underline {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background-color: #333;
    transform: scaleX(0);
    transition: all 0.3s ease;
}

.input-container input[type="number"]:focus~.underline,
.input-container input[type="number"]:valid~.underline {
    transform: scaleX(1);
}